<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card class="box-card">
      <el-button type="primary" @click="addVisible = true">添加角色</el-button>
      <!-- 添加角色弹出层 -->
      <el-dialog title="添加角色" :visible.sync="addVisible" width="50%">
        <!-- 表单区域 -->
        <el-form ref="form" :model="addForm" label-width="80px">
          <el-form-item label="角色名称">
            <el-input v-model="addForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述">
            <el-input v-model="addForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitForm">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 表格区域 -->
      <el-table :data="rolesList" style="width: 95%" stripe border>
        <el-table-column type="expand">
          <template slot-scope="scope" el-zoom-in-center>
            <el-row
              :key="item1.id"
              v-for="(item1, index1) in scope.row.children"
              :class="['bd-bm', 'flex', index1 === 0 ? 'bd-top' : '']"
            >
              <!-- 一级权限 -->
              <el-col :span="5">
                <el-tag>{{ item1.authName }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 二级权限 -->
              <el-col :span="19">
                <el-row
                  :key="item2.id"
                  v-for="(item2, index2) in item1.children"
                  :class="['flex', index2 === 0 ? '' : 'bd-top']"
                >
                  <el-col :span="7">
                    <el-tag type="success">{{ item2.authName }}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- 三级权限 -->
                  <el-col :span="12"
                    ><el-tag
                      type="warning"
                      closable
                      :key="item3.id"
                      v-for="item3 in item2.children"
                      @close="handleClose(item3.id, scope.row)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="roleName" label="角色名称"> </el-table-column>
        <el-table-column prop="roleDesc" label="角色描述"> </el-table-column>
        <el-table-column label="角色操作">
          <template slot-scope="scopes">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="small"
              @click="editRoles(scopes.row.id)"
              >编辑</el-button
            >
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="small"
              @click="changeRoots(scopes.row)"
              >分配权限</el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="small"
              @click="deleteUser(scopes.row.id)"
              >删除</el-button
            >
            <!-- 分配权限tree -->
            <el-dialog
              title="权限分配"
              :visible.sync="treeVisible"
              width="30%"
              @close="resetCheckbox"
            >
              <el-tree
                ref="keyRef"
                :data="treeData"
                :props="treeProps"
                default-expand-all
                show-checkbox
                node-key="id"
                :default-checked-keys="checkedKey"
              ></el-tree>
              <span slot="footer" class="dialog-footer">
                <el-button @click="treeVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitChecked"
                  >确 定</el-button
                >
              </span>
            </el-dialog>
            <!-- 编辑角色dialog -->
            <el-dialog
              title="角色修改"
              :visible.sync="editDialog"
              width="30%"
              @close="resetEditbox"
            >
              <!-- 表单区域 -->
              <el-form ref="editForm" :model="currentRole" label-width="80px">
                <el-form-item label="角色名称">
                  <el-input v-model="currentRole.roleName"></el-input>
                </el-form-item>
                <el-form-item label="角色描述">
                  <el-input v-model="currentRole.roleDesc"></el-input>
                </el-form-item>
              </el-form>
              <span slot="footer" class="dialog-footer">
                <el-button @click="editDialog = false">取 消</el-button>
                <el-button type="primary" @click="submitEdit">确 定</el-button>
              </span>
            </el-dialog>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rolesList: [],
      //控制添加角色弹出层
      addVisible: false,
      //添加角色表单数据
      addForm: {},
      //控制分配权限弹出层
      treeVisible: false,
      treeData: [],
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      //展示当前已有权限的树节点
      checkedKey: [],
      //当前选中权限分配的id值
      checkedId: '',
      //当前角色信息数据
      currentRole: {},
      //控制编辑弹出层的显示隐藏
      editDialog: false,
      //保存请求到的角色id
      roleId: ''
    }
  },
  methods: {
    //获取角色列表
    async getRolesList() {
      const res = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('获取角色列表失败')
      }

      this.rolesList = res.data
    },
    //添加角色功能
    async submitForm() {
      const res = await this.$http.post('roles', this.addForm)
      if (res.meta.status !== 201) return this.$message.error('添加失败!')
      this.$message.success('添加成功!')
      this.addVisible = false
      this.getRolesList()
    },
    //根据权限id和角色id删除角色权限
    handleClose(rightId, roleId) {
      this.$confirm('此操作将永久取消该权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const res = await this.$http.delete(
            `roles/${roleId.id}/rights/${rightId}`
          )

          if (res.meta.status !== 200) {
            return this.$message.error('取消权限失败')
          }
          this.$message.success('取消权限成功')
          roleId.children = res.data
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    //删除角色
    deleteUser(id) {
      this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          console.log(id)
          const res = await this.$http.delete('roles/' + id)

          if (res.meta.status !== 200) {
            return this.$message.error('删除失败')
          }
          this.$message.success('删除成功')
          this.getRolesList()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    //权限分配功能
    async changeRoots(role) {
      this.treeVisible = true
      //记录当前选中的权限分配id
      this.checkedId = role.id
      const res = await this.$http.get('rights/tree')

      if (res.meta.status !== 200) {
        return this.$message.error('获取权限列表失败')
      }
      this.showCheck(role, this.checkedKey)
      console.log(this.checkedKey)
      this.treeData = res.data
    },
    //定义递归函数,获取三级节点中的id值
    showCheck(node, arr) {
      if (!node.children) return arr.push(node.id)
      node.children.forEach(item => this.showCheck(item, arr))
    },
    //监听dialog窗口的关闭,清空选择节点数组中的值
    resetCheckbox() {
      this.checkedKey = []
    },
    //提交权限分配结果
    async submitChecked() {
      const keys = [
        ...this.$refs.keyRef.getCheckedKeys(),
        ...this.$refs.keyRef.getHalfCheckedKeys()
      ]

      const keyStr = keys.join(',')

      const res = await this.$http.post(`roles/${this.checkedId}/rights`, {
        rids: keyStr
      })

      if (res.meta.status !== 200) return this.$message.error('分配权限失败')

      this.$message.success('分配权限成功')
      this.treeVisible = false
      this.getRolesList()
    },
    //点击编辑功能渲染当前id下的数据
    async editRoles(id) {
      const res = await this.$http.get('roles/' + id)

      if (res.meta.status !== 200) {
        return this.$message.error('获取当前角色信息失败')
      }
      this.editDialog = true
      this.currentRole = res.data
      this.roleId = res.data.roleId
    },
    //关闭弹出层时清除表单数据
    resetEditbox() {
      this.$refs.editForm.resetFields()
    },
    //提交角色修改
    async submitEdit() {
      const res = await this.$http.put('roles/' + this.roleId, {
        roleName: this.currentRole.roleName,
        roleDesc: this.currentRole.roleDesc
      })

      if (res.meta.status !== 200) {
        return this.$message.error('修改失败')
      }
      this.$message.success('修改成功!')
      this.editDialog = false
      this.getRolesList()
    },
    // 删除角色功能
    
  },
  mounted() {
    this.getRolesList()
  }
}
</script>

<style lang="css" scoped>
.el-tag {
  margin: 7px 0;
}
.bd-top {
  border-top: 1px solid #eee;
}
.bd-bm {
  border-bottom: 1px solid #eee;
}
.flex {
  display: flex;
  align-items: center;
}
</style>
